@import "variables";

@import "components/home";
@import "components/header";
@import "components/nav";
@import "components/button";
@import "components/content";

html {
  height: 100%;
  font-size: 16px;
  background: url('../img/bg_image.jpg') no-repeat right 31.5% bottom 66% fixed;
  background-size: 180%;
  @include basic;

  @media (max-width: 1024px) {
    font-size: calc(16px + .4vw);
    background: url('../img/bg_image.jpg') no-repeat right 34% bottom 65% fixed;
    background-size: 190%; }
  @media (max-width: 320px) {
    background: url('../img/bg_image.jpg') no-repeat right 38% bottom 66% fixed;
    background-size: 350%; } }
body {
  display: flex;
  flex-direction: column;
  height: 100vh;
  margin: 0;
  background-color: rgba(#000000, 0.33); }

a:link, a:visited {
  color: white;
  text-decoration: none; }
