.content {
  display: flex;
  flex-direction: column;
  width: 555px;
  height: 600px;
  margin: 0 auto;
  &__text {
    margin-top: 1.85em;
    text-align: center;
    @include raleway__light;
    line-height: 1.2em; }
  &__text-bold-white {
    @include raleway__bold;
    color: white;
    line-height: 1.3em; }
  &__icon {
    color: white;
    height: 104px;
    width: 104px;
    margin: 3.1em auto;
    //&:hover
 } }    ////animation: spin .8s infinite linear
.social {
  margin: 1em 0;
  &__wrapper {
    margin: 1.9em 1em 0 1em;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    @include basic;
    letter-spacing: 1px;
    font-size: .6rem;
    color: white;
    text-transform: uppercase;
    -webkit-padding-start: 0; }
  &__text {
    @include basic;
    text-align: center;
    color: white;
    font-size: .7rem; }
  &__item {
    margin: 0;
    display: flex;
    cursor: pointer;
    &:hover {
      opacity: .8; } }

  &__icon {
    display: inline-block;
    width: auto;
    height: 24px; }
  &__icon-g {
    display: inline-block;
    width: auto;
    height: 24px; }
  &__wrapper {
    @media (max-width: 475px) {
      flex-direction: column; } } }
.spacer {
  display: inline-block;
  margin: 0 8px;
  padding: 0 2px; }

.rotate {
  animation: rotate 1s; }
@keyframes rotate {
  from {
    transform: rotate(0deg); }
  to {
    transform: rotate(360deg); } }

.triangle {
  position: fixed;
  text-align: center;
  bottom: -3.5em;
  left: 50%;
  transform: translateX(-50%);
  width: 4.93em;
  height: 4.93em;
  cursor: pointer;
  &:hover {
    opacity: .8; }
  &__bg {
    position: absolute;
    content: "";
    background: white;
    width: 4.93em;
    height: 4.93em;
    transform: rotate(-45deg);
    border-top-right-radius: 5%;
    z-index: -1; }
  &__text {
    position: relative;
    margin-top: .26em;
    z-index: 1;
    @include basic;
    color: black;
    font-size: .55em; }
  &__plus {
    display: none;
    position: relative;
    bottom: .5em;
    z-index: 1;
    @include basic;
    color: black;
    font-size: 1em; } }


@media (max-width: 1024px) {
  .content {
    width: 435px;
    margin: -0.2em auto;
    &__icon {
      height: 82px;
      width: 82px;
      margin: 3.83em auto; } }
  .social {
    margin: 1.05em 0;
    letter-spacing: .07px;
    &__wrapper {
      margin: 2.5em 1em;
      letter-spacing: 0; }
    &__icon,  &__icon-g {
      height: 19px; } }

  .triangle {
    bottom: -3.25em; } }

@media (max-width: 320px) {
  .content {
    width: 220px;
    height: auto;
    &__text {
      font-size: 18.5px;
      margin: 21px 0px 20px 0px;
      line-height: 1.25em;
      padding: 0 .20em; }
    &__text-2 {
      padding: 0 1.7em; }
    &__text-bold-white {
      line-height: 1.3em;
      font-weight: 200;
      font-size: 18.5px; }
    &__icon {
      height: 65px;
      margin: auto; } }

  .social__wrapper {
    justify-content: space-between;
    align-content: center;
    height: 13em;
    margin: 18px 0; }

  .social {

    &__text {
      font-size: 10px;
      margin: 4px 2em;
      line-height: 18px; }
    &__item {
      font-size: 12px;
      margin: auto; }
    &__icon,  &__icon-g {
      height: 15px;
      padding: 0 4px; } }
  .spacer {
    padding: 0 4px;
    margin: 0 4px 0 4px; }

  .triangle {
    width: 4.1em;
    height: 4.1em;
    &__bg {
      width: 4.1em;
      height: 4.1em; }
    &__text {
      display: none; }
    &__plus {
      display: inline;
      bottom: .6em; } } }
