.mobile-nav__toggle {
  display: none;
  width: 28px;
  align-items: center;
  cursor: pointer;
  z-index: 99999;
  padding: 12px 0;
  &:selection {
    background: transparent; }
  &:-moz-selection {
    background: transparent; }
  span,
  span:before,
  span:after {
    content: "";
    display: block;
    height: 4px;
    width: 100%;
    border-radius: 2px;
    background: #fff;
    position: relative;
    transition: all 0.3s ease-in-out; }

  span:before {
    top: 9px; }
  span:after {
    top: -13px; }

  &.is-open span {
    background: transparent; }

  &.is-open span:before,
  &.is-open span:after {
    transform-origin: center;
    transform: rotate(405deg) translate(-1px, 0px);
    top: 0; }

  &.is-open span:before {
    transform: rotate(-405deg) translate(-2px, 2px);

    ul {
      list-style: none;
      margin: 0;
      padding: 0;
      padding-top: 9em; }
    li {
      text-align: center;
      margin-bottom: 10px; } } }




.nav {
  display: inline;
  position: fixed;
  right: 4.9em;
  top: 3.66em;
  width: 41.3%;
  color: white;
  text-transform: uppercase;
  font-size: 0.63rem;
  &__list {
    display: flex;
    justify-content: space-between;
    text-decoration: none;
    list-style-type: none;
    margin: 0 -12px; }
  &__item {
    margin: 0 12px; } }

.mobile-nav {
  display: none;
  position: fixed;
  height: 530px;
  width: 100vw;
  z-index: 9998;
  background: linear-gradient(to top right, (darken(#001820, 4%), lighten(#001820, 4%)));
  padding: 0;
  transform: translateY(-100%);
  transition: all 0.3s ease-in-out; }




@media (max-width: 1024px) {
  .nav {
    top: 4.2em;
    right: 3.1em;
    width: 47%; } }

@media (max-width: 575px) {
  .nav {
    display: none; }
  .mobile-nav__toggle {
    display: block;
    position: fixed;
    top: 19px;
    right: 23px; } }

.mobile-nav.is-open {
    display: flex;
    flex-direction: column;
    transform: translateY(0);
    text-transform: uppercase; }
.mobile-nav {
  &__list {
    display: flex;
    -webkit-padding-start: 0;
    width: 100%;
    height: 261px;
    margin: 112px auto 68px auto;
    flex-direction: column;
    //align-content: space-between
    justify-content: space-between;
    text-align: center;
    text-decoration: none;
    list-style-type: none; }

  &__item {} }



.mobile-nav-social {
  flex-direction: row;
  &__wrapper {
    margin: 0 25px;
    display: flex;
    flex-direction: row;
    justify-content: space-between; }
  &__icon, &__icon-g {
    margin: auto;
    height: 22px;
    flex: 1;
    &:hoover {
      opacity: 0.7; } } }
